@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  box-sizing: border-box;
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAIElEQVQoU2P88OHDfwYiACNIIQsLC0GlowrxBhHRwQMAjJUmjWW+XhkAAAAASUVORK5CYII=) repeat;
  border: .5em solid var(--theme);

  --move: #3f51b5;
  --edit: #ef4327;
  --plan: #00cd82;
}

.app.move {
  --theme: var(--move);
}

.app.edit {
  --theme: var(--edit);
}

.app.plan {
  --theme: var(--plan);
}

.app .title {
  position: absolute;
  left: -.25em;
  top: -.25em;
}

.app .router {
  width: 100%;
  height: 100%;
}

.title {
  z-index: 10;
  display: inline-block;
  margin: 0;
  padding: .5em 1em;
  background: var(--theme);
  font-size: 1.5em;
  color: #fff;
}

.dial {
  z-index: 3;
  position: absolute;
  right: 1.5em;
  bottom: 1.5em;
}

.dial .MuiFab-primary, .dial .MuiFab-primary:hover {
  background-color: var(--theme);
  -webkit-transition: background-color .3s;
  transition: background-color .3s;
}

.graph {
  position: relative;
  width: 100%;
  height: 100%;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAIElEQVQoU2P88OHDfwYiACNIIQsLC0GlowrxBhHRwQMAjJUmjWW+XhkAAAAASUVORK5CYII=) repeat;
}

.graph > rect {
  fill: rgba(255, 255, 255, 0);
}

.cross line {
  stroke: var(--theme);
  stroke-width: 1;
}

.node {
  z-index: 1;
  position: relative;
  cursor: pointer;
}

.node.active {
  z-index: 2;
}

.node foreignObject {
  x: -40px;
  y: -40px;
  width: 80px;
  height: 80px;
}

.node div {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 50%;
  height: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: var(--theme);
  border-radius: 50%;
  box-shadow: 0 0 .5em -.25em rgba(0, 0, 0, .6);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #fff;
  -webkit-transition: background .3s, box-shadow .3s, -webkit-transform .3s;
  transition: background .3s, box-shadow .3s, -webkit-transform .3s;
  transition: background .3s, box-shadow .3s, transform .3s;
  transition: background .3s, box-shadow .3s, transform .3s, -webkit-transform .3s;
}

.node.move {
  cursor: -webkit-grab;
  cursor: grab;
}

.node.move.dragging {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.node.move:hover div {
  box-shadow: 0 0 .25em .125em rgba(0, 0, 0, .2);
  -webkit-transform: translate(-50%, -50%) scale(1.0625);
          transform: translate(-50%, -50%) scale(1.0625);
}

.node.move.dragging div {
  box-shadow: 0 0 .5em .125em rgba(0, 0, 0, .2);
  -webkit-transform: translate(-50%, -50%) scale(1.125);
          transform: translate(-50%, -50%) scale(1.125);
}

.node.edit .delete {
  position: absolute;
  opacity: 0;
}

.node.edit:hover .delete {
  opacity: 1;
}

.node.edit .hook {
  position: absolute;
  display: block;
  left: 50%;
  top: 50%;
  opacity: 0;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}

.node.edit .hook:after {
  content: '';
  position: absolute;
  width: .8em;
  height: .8em;
  background: #333;
  border-radius: .8em;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.node.edit div:hover .hook,
.node.edit.active .hook {
  opacity: 1;
}

.node.edit div:focus-within .hook {
  opacity: 0;
}

.node.plan div {
  background: #888;
}

.node.plan.active div {
  background: var(--theme);
}

.edge {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.edge line {
  stroke: #ccc;
  stroke-dasharray: 4;
  stroke-width: 2;
}

.edge polygon {
  fill: #ccc;
}

.edge foreignObject {
  x: -30px;
  y: -30px;
  width: 60px;
  height: 60px;
}

.edge foreignObject > div {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 40%;
  height: 35%;
  background: #777;
  border-radius: 2px;
  color: #eee;
  font-size: .8em;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.edge.move {
  cursor: -webkit-grab;
  cursor: grab;
}

.edge.move.dragging {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.edge.edit .delete {
  position: absolute;
  opacity: 0;
}

.edge.edit foreignObject:hover .delete {
  opacity: 1;
}


.edge.plan {
  opacity: .5;
}

.edge.plan line {
  stroke: #555;
}

.edge.plan polygon {
  fill: #555;
}

.edge.plan.highlight line {
  -webkit-animation: dash 1s linear infinite;
          animation: dash 1s linear infinite;
}

.edge.plan.selected {
  opacity: 1;
}

@-webkit-keyframes dash {
  to {
    stroke-dashoffset: -50;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: -50;
  }
}

.edge.selected line {
  stroke: var(--theme);
  stroke-dasharray: 0;
}

.edge.selected polygon {
  fill: var(--theme);
}

.input {
  display: block;
  width: 60%;
  background: transparent;
  border: none;
  border-bottom: 1px solid currentColor;
  color: inherit;
  font-size: 1em;
  text-align: center;
  outline: none;
  -webkit-transition: border-color .3s;
  transition: border-color .3s;
}

.input[readonly] {
  border-color: transparent;
}

.delete {
  display: inline-block;
  bottom: 75%;
  left: 75%;
  padding: .125em;
  background-color: #cc0f00;
  border-radius: 100% 100% 100% .5em;
  box-shadow: 0 0 .25em -.125em rgba(0, 0, 0, 1);
  color: #fff;
  -webkit-transition: opacity .3s, background-color .3s;
  transition: opacity .3s, background-color .3s;
}

.delete:hover {
  background:#ee0f00;
}

.delete > svg {
  display: block;
  width: 16px;
  height: 16px;
}

.report {
  z-index: 9;
  box-sizing: border-box;
  overflow: hidden;
  position: absolute;
  width: 25vw;
  min-width: 10em;
  max-width: 30em;
  padding: 4em 1.25em 1em 1em;
  -webkit-transform: translate(-100%, 0);
          transform: translate(-100%, 0);
  -webkit-transition: all .6s;
  transition: all .6s;
}

.report.visible {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

.report h1 {
  margin-top: 0;
}

.report dl {
  display: grid;
  grid-template-columns: auto -webkit-min-content;
  grid-template-columns: auto min-content;
  margin: 0;
  background: #fff;
}

.report dt, .report dd {
  margin: 0;
  margin-top: -1px;
  padding: .5em;
  border: 1px solid #dfdfdf;
}

.report dd {
  margin-left: -1px;
  text-align: center;
}

.report dl :first-of-type {
  font-weight: bold;
  background: #f4f4f4;
}

.report dl :last-of-type {
  font-weight: bold;
}

.report dt:last-of-type {
  padding-right: 1em;
  text-align: right;
}

.report h2 {
  margin-right: .5em;
  text-align: right;
}

